const baseUrl = "https://localhost:443/webapi";
import Axios from 'axios'

export default {
    login(admin, pwd) {
        return new Promise(function(resolve,reject){
            Axios
            .post(baseUrl + "/login", {
                name: admin,
                password: pwd
            })
            .then(response => {
                resolve(response.data);
            })
            .catch(response => {
                reject(response);
            })
        })
    },
    adrsByDay(day) {
        return new Promise(function(resolve,reject) {
            Axios
            .get(baseUrl + "/dstb?date=" + day)
            .then(response => {
                resolve(response);
            })
            .catch(response => {
                reject(response);
            })
        })
    }
}